// Home.js

import React, {
  useState,
  useEffect,
  setState,
  Component,
  useContext,
} from "react";
import "semantic-ui-css/semantic.min.css";
import "date-fns";
import "./App.css";
import { storage } from "./firebase/firebase";
import {
  Container,
  Button,
  Popup,
  Image,
  Input,
  Label,
  Icon,
  TextArea,
  Form,
  Radio,
  Card,
} from "semantic-ui-react";
import DateFnsUtils from "@date-io/date-fns";
import { GoogleSpreadsheet } from "google-spreadsheet";
import Logo from "./images/logo.png";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { UserContext } from "./providers/UserProvider";

function Registros({ usuario, setUsuario, session, setSession }) {
  const user = useContext(UserContext);
  const { Nombre, Correo, Apellido } = user;
  console.log(user);
  const [width, setWidth] = useState(window.innerWidth);
  const [height, setHeight] = useState(window.innerHeight);
  const updateWidthAndHeight = () => {
    setWidth(window.innerWidth);
    setHeight(window.innerHeight);
  };
  const areas = [
    { id: 1, name: "Area de Reciclaje", short: "AR" },
    { id: 2, name: "Linea1", short: "L1" },
    { id: 3, name: "Linea2", short: "L2" },
    { id: 4, name: "Linea3", short: "L3" },
    { id: 5, name: "Linea4", short: "L4" },
    { id: 6, name: "Linea5", short: "L5" },
    { id: 7, name: "PTAR", short: "PT" },
    { id: 8, name: "Sala de Jarabe", short: "SJ" },
    { id: 9, name: "Taller de Mtto", short: "TM" },
    { id: 9, name: "Area de Operaciones", short: "OP" },
  ];
  const cats = [
    { id: 1, name: "Calidad", short: "Q" },
    { id: 2, name: "Seguridad", short: "S" },
    { id: 3, name: "Ambiental", short: "A" },
    { id: 4, name: "BioSeguridad", short: "B" },
  ];
  const srisk = [
    { id: 1, name: "Sin EPP", src: "epp.png", short: "Uso EPP" },
    {
      id: 2,
      name: "Riesgo Seg. Electrica",
      src: "peligro.png",
      short: "R. Electrico",
    },
    {
      id: 3,
      name: "Riesgo de Tropiezo",
      src: "resbalar.png",
      short: "R. Tropiezo",
    },
    {
      id: 4,
      name: "Riesgo de Quemaduras",
      src: "quemadura.png",
      short: "R. Quemadura",
    },
    {
      id: 5,
      name: "Riesgo de Cortadura",
      src: "cortadura.png",
      short: "R. Cortadura",
    },
    {
      id: 6,
      name: "Uso de Paso Peatonal",
      src: "pasopeatonal.png",
      short: "Uso PasoPeatonal",
    },
    {
      id: 7,
      name: "Uso de Celular en Area No Autorizada",
      src: "mobile.png",
      short: "Uso Celular",
    },
    {
      id: 8,
      name: "Acto Inseguro",
      src: "precaucion.png",
      short: "Acto Inseguro",
    },
    {
      id: 9,
      name: "Sin Eq. de Prot. de Maquinaria",
      src: "seguridad_maq.png",
      short: "Prot. Maquinaria",
    },
    {
      id: 10,
      name: "Riesgo de Contacto con Quimico Peligroso",
      src: "quimico.png",
      short: "R. Quimico",
    },
  ];
  const corona = [
    {
      id: 1,
      name: "Disntanciamiento Fisico",
      short: "Disntanciamiento Fisico",
      src: "distancia.png",
    },
    {
      id: 2,
      name: "Uso de Mascarilla",
      short: "Uso de Mascarilla",
      src: "mascarilla.png",
    },
    {
      id: 3,
      name: "Senalizacion de Bioseguridad",
      short: "Senalizacion de Bioseguridad",
      src: "covid-sign.png",
    },
    {
      id: 4,
      name: "Control de Entrada",
      short: "Control de Entrada",
      src: "control.png",
    },
    {
      id: 5,
      name: "Uniforme Limpio",
      short: "Uniforme Limpio",
      src: "uniforme.png",
    },
    {
      id: 6,
      name: "Lavado de Manos correcto",
      short: "Lavado de Manos correcto",
      src: "lavado-a-mano.png",
    },
    {
      id: 7,
      name: "Articulos de Higiene en estaciones",
      short: "Articulos de Higiene en estaciones",
      src: "art-limpieza.png",
    },
  ];
  const calidad = [
    {
      id: 1,
      name: "Salon de llenado con desechos de corrida previa",
      src: "desechos.png",
    },
    { id: 2, name: "Paredes y techos sin limpieza", src: "suelo.png" },
    { id: 3, name: "Piso con agua empozada y acumulada", src: "alerta.png" },
    {
      id: 4,
      name: "Equipo de Proceso con falta de higiene Visible",
      src: "maquinaria.png",
    },
    {
      id: 5,
      name: "Proceso sin Control de Acceso funcionando",
      src: "filtrar.png",
    },
    { id: 6, name: "Presencia de Plagas en area de Proceso", src: "rata.png" },
  ];
  const ambiente = [
    {
      id: 1,
      name: "Acumulacion de residuo solidos del proces",
      short: "Acumulacion R.S.",
      src: "desechos.png",
    },
    {
      id: 2,
      name: "Clasificacion incorrecta de los residuo solidos",
      short: "Clasificacion R.S.",
      src: "reciclaje.png",
    },
    {
      id: 3,
      name: "Manejo incorrecto de materiales peligroso",
      short: "Manejo de M.Peligroso",
      src: "residuo_peligroso.png",
    },
    {
      id: 4,
      name: "Fuga de agua significativa observada",
      short: "Fuga de Agua",
      src: "fuga.png",
    },
    {
      id: 5,
      name: "Proceso con consumo descontrolado de energia",
      short: "Control Consumo de Energia",
      src: "electric.png",
    },
  ];
  //Fechas//
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  let fecha = date + " " + hora + ":" + minutos;
  //Fechas//
  const [startDate, setStartDate] = useState(new Date());
  let sArea = "";
  let sCat = "";
  let sRubro = "";
  let sObs = "";
  let sPac = "";
  if (typeof sessionStorage.area === "undefined") {
    sArea = "";
  } else {
    sArea = sessionStorage.area;
  }
  if (typeof sessionStorage.cat === "undefined") {
    sCat = "";
  } else {
    sCat = sessionStorage.cat;
  }
  if (typeof sessionStorage.rubro === "undefined") {
    sRubro = "";
  } else {
    sRubro = sessionStorage.rubro;
  }
  if (typeof sessionStorage.obs === "undefined") {
    sObs = "";
  } else {
    sObs = sessionStorage.obs;
  }
  if (typeof sessionStorage.pac === "undefined") {
    sPac = "";
  } else {
    sPac = sessionStorage.pac;
  }
  const [area, setArea] = useState(sArea);
  const [cat, setCat] = useState(sCat);
  const [rubro, setRubro] = useState(sRubro);
  const [obs, setObs] = useState(sObs);
  const [pac, setPac] = useState(sPac);
  const allInputs = { imgUrl: "" };
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const handleImageAsFile = (e) => {
    const image = e.target.files[0];
    setImageAsFile((imageFile) => image);
    setSubir(true);
  };
  const handleFireBaseUpload = (e) => {
    e.preventDefault();
    console.log("start of upload");
    if (imageAsFile === "") {
      console.error(`not an image, the image file is a ${typeof imageAsFile}`);
    }
    const uploadTask = storage
      .ref(`/images/${imageAsFile.name}`)
      .put(imageAsFile);
    uploadTask.on(
      "state_changed",
      (snapShot) => {
        //takes a snap shot of the process as it is happening
        console.log(snapShot);
      },
      (err) => {
        //catches the errors
        console.log(err);
      },
      () => {
        // gets the functions from storage refences the image storage in firebase by the children
        // gets the download url then sets the image from firebase as the value for the imgUrl key:
        storage
          .ref("images")
          .child(imageAsFile.name)
          .getDownloadURL()
          .then((fireBaseUrl) => {
            setImageAsUrl((prevObject) => ({
              ...prevObject,
              imgUrl: fireBaseUrl,
            }));
          });
      }
    );
  };
  const [logged, setLogged] = useState(session);
  return (
    <Container>
      <h2>
        Areas{" "}
        {sArea === "" || sArea === "undefined" ? (
          <Icon color="red" name="times circle outline" size="large" />
        ) : (
          <Icon color="green" name="check circle outline" size="large" />
        )}
      </h2>
      <div class="scrolling-wrapper">
        {areas.map((item, i) => (
          <Button
            inverted
            color="red"
            content={item.name}
            value={item.name}
            onClick={(e) => {
              setArea(e.target.value);
              sessionStorage.area = e.target.value;
            }}
          >
            {area === item.name ? (
              <Label as="a" color="red">
                {item.name}
                <Icon name="check circle outline" />
              </Label>
            ) : null}
          </Button>
        ))}
      </div>

      <h2>
        Catergorias{" "}
        {sCat === "" || sCat === "undefined" ? (
          <Icon color="red" name="times circle outline" size="large" />
        ) : (
          <Icon color="green" name="check circle outline" size="large" />
        )}
      </h2>
      <div class="scrolling-wrapper">
        {cats.map((item, i) => (
          <Button
            inverted
            color="red"
            content={item.name}
            value={item.name}
            onClick={(e) => {
              setCat(e.target.value);
              sessionStorage.cat = e.target.value;
              setRubro("");
              sessionStorage.rubro = "";
            }}
          >
            {cat === item.name ? (
              <Label as="a" color="red">
                {item.name}
                <Icon name="check circle outline" />
              </Label>
            ) : null}
          </Button>
        ))}
      </div>
      <br />
      {sCat === "" || sCat === "undefined" ? null : (
        <span>
          <h2>
            {" "}
            Rubros{" "}
            {sRubro === "" || sRubro === "undefined" ? (
              <Icon color="red" name="times circle outline" size="large" />
            ) : (
              <Icon color="green" name="check circle outline" size="large" />
            )}
          </h2>
          <p>{sRubro === "" || sRubro === "undefined" ? null : sRubro}</p>
        </span>
      )}
      <br />

      <div class="pegado">
        {cat === "Calidad"
          ? calidad.map((item, i) => (
              <p style={{ marginLeft: "2rem" }}>
                <Popup
                  content={item.name}
                  key={item.id}
                  trigger={
                    <Image
                      src={require(`./images/${item.src}`)}
                      size="tiny"
                      className={item.name}
                      onClick={(e) => {
                        setRubro(
                          e.target.className.replace("ui tiny image ", "")
                        );
                        sessionStorage.rubro = e.target.className.replace(
                          "ui tiny image ",
                          ""
                        );
                        setObs("");
                        sessionStorage.obs = "";
                        setPac("");
                        sessionStorage.pac = "";
                      }}
                    />
                  }
                />
              </p>
            ))
          : null}
        {cat === "Ambiental"
          ? ambiente.map((item, i) => (
              <p style={{ marginLeft: "2rem" }}>
                <Popup
                  content={item.name}
                  key={item.id}
                  trigger={
                    <Image
                      src={require(`./images/${item.src}`)}
                      size="tiny"
                      className={item.name}
                      onClick={(e) => {
                        setRubro(
                          e.target.className.replace("ui tiny image ", "")
                        );
                        sessionStorage.rubro = e.target.className.replace(
                          "ui tiny image ",
                          ""
                        );
                        setObs("");
                        sessionStorage.obs = "";
                        setPac("");
                        sessionStorage.pac = "";
                      }}
                    />
                  }
                />
              </p>
            ))
          : null}
        {cat === "Seguridad"
          ? srisk.map((item, i) => (
              <p style={{ marginLeft: "2rem" }}>
                <Popup
                  content={item.name}
                  key={item.id}
                  trigger={
                    <Image
                      src={require(`./images/${item.src}`)}
                      size="tiny"
                      className={item.name}
                      onClick={(e) => {
                        setRubro(
                          e.target.className.replace("ui tiny image ", "")
                        );
                        sessionStorage.rubro = e.target.className.replace(
                          "ui tiny image ",
                          ""
                        );
                        setObs("");
                        sessionStorage.obs = "";
                        setPac("");
                        sessionStorage.pac = "";
                      }}
                    />
                  }
                />
              </p>
            ))
          : null}
        {cat === "BioSeguridad"
          ? corona.map((item, i) => (
              <p style={{ marginLeft: "2rem" }}>
                <Popup
                  content={item.name}
                  key={item.id}
                  trigger={
                    <Image
                      src={require(`./images/${item.src}`)}
                      size="tiny"
                      className={item.name}
                      onClick={(e) => {
                        setRubro(
                          e.target.className.replace("ui tiny image ", "")
                        );
                        sessionStorage.rubro = e.target.className.replace(
                          "ui tiny image ",
                          ""
                        );
                        setObs("");
                        sessionStorage.obs = "";
                        setPac("");
                        sessionStorage.pac = "";
                      }}
                    />
                  }
                />
              </p>
            ))
          : null}
      </div>
      <br />
      {sRubro === "" || sRubro === "undefined" ? null : (
        <span>
          <h2>
            Observacion{" "}
            {sObs === "" || sObs === "undefined" ? (
              <Icon color="red" name="times circle outline" size="large" />
            ) : (
              <Icon color="green" name="check circle outline" size="large" />
            )}
          </h2>
          <p>
            <form onSubmit={handleFireBaseUpload}>
              <Input
                disabled={subir}
                fluid
                className="input-image"
                type="file"
                action={{
                  content: "Subir Imagen",
                  icon: "camera",
                }}
                onChange={handleImageAsFile}
              />
              <br />
              <Image src={imageAsUrl.imgUrl} size="small" />
            </form>
          </p>
          <Form>
            <TextArea
              fluid
              value={sObs}
              onChange={(e) => {
                setObs(e.target.value);
                sessionStorage.obs = e.target.value;
              }}
            />
          </Form>
          <br />
          <h2>
            Plan de Accion{" "}
            {sPac === "" || sPac === "undefined" ? (
              <Icon color="red" name="times circle outline" size="large" />
            ) : (
              <Icon color="green" name="check circle outline" size="large" />
            )}
          </h2>
          <p>
            <Input
              type="date"
              onChange={(e) => setStartDate(e.target.value)}
              defaultValue={date}
            />
          </p>
          <Form>
            <TextArea
              fluid
              value={sPac}
              onChange={(e) => {
                setPac(e.target.value);
                sessionStorage.pac = e.target.value;
              }}
            ></TextArea>
          </Form>
        </span>
      )}
      <br />
      <Button
        fluid
        size="large"
        icon="save"
        content="Save"
        color="red"
        disabled={
          area === "" || cat === "" || rubro === "" || obs === "" || pac === ""
        }
        ///onClick={()=>{console.log((area===''||cat===''||rubro===''||obs===''||pac===''||area==='undefined'||cat==='undefined'||rubro==='undefined'||obs==='undefined'||pac==='undefined'))}}
      ></Button>
    </Container>
  );
}

export default Registros;
